@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
*{box-sizing:inherit;margin:0;padding:0}*::before,*::after{box-sizing:inherit}:root{box-sizing:border-box}body{background:#f5f5f5;background:var(--theme-background-color, #f5f5f5);color:rgba(0,0,0,0.9);color:var(--theme-text-color, rgba(0,0,0,0.9));font-family:sans-serif}a{color:#438ae4;color:var(--theme-link-color, #438ae4)}a:visited{color:#a35dba;color:var(--theme-link-color, #a35dba)}button{-webkit-appearance:none;appearance:none;border:none;font:inherit}p,ul,ol,dl{line-height:1.8;margin-bottom:1em}img{display:block;max-width:100%}:focus{outline:3px solid lightsteelblue}.site-header{-webkit-align-items:center;align-items:center;background:#222;display:-webkit-flex;display:flex;height:50px;padding:.5rem 1rem;width:100%}.site-name{color:rgba(255,255,255,0.8);contain:content;font-family:"Space Mono", monospace;font-size:1.25rem;font-weight:400;text-transform:uppercase;transition:.15s color ease-in;will-change:color}.site-name:hover{color:#fff}.site-name a{color:inherit;text-decoration:none}

